<template>
    <div>
        <top></top>
        <!---->
        <div :style="{ background: 'url(' + img + ')', width: '100%', height: '450px', 'background-position-y': '-275px' }"
            :url="img"></div>
        <div class="container" style="margin:60px auto;margin-bottom:105px">
            <div class="row" style="display: flex;align-items: center;">
                <div class="col-sm-2">
                    <ul class="menu">
                        <li data-index="0" class="menu-item" :class="{ active: showlianxi }" style="margin-top:-18px;"
                            @click="keydown(1)">
                            <span>关于我</span>
                        </li>
                        <li data-index="1" class="menu-item" :class="{ active: !showlianxi }" @click="keydown(2)">
                            <span>联系我</span>
                        </li>
                    </ul>
                </div>
                <div class="col-sm-10" style="height: 80px;padding-left: 15px;">
                    <div class="desc desc-1 active" style="height: 44px; display: block;">
                        <div v-if="showlianxi">
                            <span>染七（Randomseven），中学生，自学HTML、CSS、Javascript，会使用PHP、NodeJS等语言。</span>
                        </div>
                        <div v-else>
                            <span>微信平台<span title="chat"></span></span>
                            <span>QQ：985189328</span>
                            <span>其他平台均为：Randomseven</span>
                        </div>
                    </div>
                </div>
            </div>
        </div>
        <!---->
        <footers></footers>
    </div>
</template>
    







<script>
import top from '../../components/topnav_for_other.vue';
import footers from '../../components/footer.vue';
export default {
    name: "App",
    components: {
        top,
        footers,
    },
    data() {
        return {
            img: require("../../assets/index_bg.jpg"),
            showlianxi: true,
        }
    },
    mounted() {
        var url = window.location.href;             //获取当前url

        var dz_url = url.split('#')[0];                //获取#/之前的字符串

        var cs = dz_url.split('?')[1];                //获取?之后的参数字符串

        var cs_arr = cs.split('&');                    //参数字符串分割为数组

        var csw = {};

        for (var i = 0; i < cs_arr.length; i++) {         //遍历数组，拿到json对象

            csw[cs_arr[i].split('=')[0]] = cs_arr[i].split('=')[1]

        }
        //csw 全部参数
        var id = csw.id
        if (id == "1") {
            this.keydown(1)
        }
        else {
            this.keydown(2)
        }
    },
    methods: {
        keydown(e) {
            var that = this
            if (e === 1) {
                that.$data.showlianxi = true;
                
            }
            else {
                that.$data.showlianxi = false;
            }
        }
    },
};
</script>
